

@keyframes float {
	0% {
		box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6);
		transform: translatey(0px);
	}
	50% {
		box-shadow: 0 25px 15px 0px rgba(0, 0, 0, 0.2);
		transform: translatey(-20px);
	}
	100% {
		box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6);
		transform: translatey(0px);
	}
}




.bioContainer {
	text-align: center;
	padding: 20px;
    background-color: aliceblue;
    border-radius: 20px;
    min-height: 400px;
	overflow: hidden;
	box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6);
	transform: translatey(0px);
	animation: float 6s ease-in-out infinite;
	color: rgb(10, 71, 109);
}


@keyframes floatnight {
	0% {
		box-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #0ba9ca, 0 0 0px #0ba9ca,
    0 0 40px #0ba9ca, 0 0 50px #0ba9ca;
		transform: translatey(0px);
	}
	50% {
		box-shadow: 0 0 25px #fff, 0 0 10px #fff, 0 0 15px #0ba9ca, 0 0 0px #0ba9ca,
    0 0 40px #0ba9ca, 0 0 50px #0ba9ca;
		transform: translatey(-20px);
	}
	100% {
		box-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #0ba9ca, 0 0 0px #0ba9ca,
    0 0 40px #0ba9ca, 0 0 50px #0ba9ca;
		transform: translatey(0px);
	}
}
.bioContainerDark {
	padding: 20px;
	text-align: center;
    background-color: rgba(12, 13, 14, 0.425);
    border-radius: 20px;
    min-height: 400px;
	overflow: hidden;
	color: #fff;
	box-shadow: 0 0 2px #fff, 0 0 10px #fff, 0 0 20px #0ba9ca, 0 0 30px #0ba9ca,
    0 0 40px #0ba9ca, 0 0 50px #0ba9ca;
	transform: translatey(0px);
	animation: floatnight 6s ease-in-out infinite;
	
}
.neonText {
	animation: flicker 1.5s infinite alternate;
	color: #fff;
  }
.simptext{
	color : rgb(20, 132, 202);
}
  
  
  /* Flickering animation */
  @keyframes flicker {
	  
	0%, 18%, 22%, 25%, 53%, 57%, 100% {
  
		text-shadow:
		0 0 4px rgb(20, 132, 202),
		0 0 11px rgb(20, 132, 202),
		0 0 19px rgb(20, 132, 202),
		0 0 40px rgb(20, 132, 202),
		0 0 80px rgb(20, 132, 202),
		0 0 90px rgb(20, 132, 202),
		0 0 100px rgb(20, 132, 202),
		0 0 150px rgb(20, 132, 202);
	
	}
	
	20%, 24%, 55% {        
		text-shadow: none;
	}    
  }
  

